* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}
input:focus,input{
  outline: none;
  border:none;
}
button{
  border:none;
  cursor: pointer;
  background-color: transparent;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}


@keyframes slidedown {
  from {
    max-height: 0;
  }
  to {
    max-height: 100vh;
  }
}
@keyframes text {
  from {
    transform: translateX(50px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes opa {
  from {
    transform: scale(1.05);

    -webkit-filter: saturate(.7);

            filter: saturate(.7);
  }
  to {
    transform: scale(1);
    -webkit-filter: saturate(1.3);
            filter: saturate(1.3);
  }
}

:root {
  /* varialbe declaration! */
  --primary-background: #5dc269;
  --secondary-background: #253526;
  --secondary-background-trans: #253526ec;
  --primary-text: #ffffff;
  --button-primary: #ecb338;
  --text-button-primary: #6d5b1c;
  --button-secondary: #9fb86f;
  --text-button-secondary: #fff;
  --text-background: #63b96d86;
  --banner-primary-background: #2d2418;

  /* dimensions */
  --medium-nav: 950px;

  /* animation */
  --slide-down: slidedown 0.7s linear forwards;
  --hide: hide 0.2s forwards linear;
  --appear: text 1.1s ease forwards;
  --opacity: opa 5s linear forwards;

  /* fonts */
  --font-primary: "Poppins", sans-serif;
  --font-banner: "Bebas Neue", cursive;
}
body{
  font-family: "Poppins", sans-serif;
  font-family: var(--font-primary);
}
.button {
  border: none;
  border-radius: 4px;
  /* background-color: var(--button-primary); */
}
.bg-second{
  background-color: #253526ec !important;
  background-color: var(--secondary-background-trans) !important;
  color:white !important;
}

::selection {
  color: white;
  background: #253526d5;
}
.swiper-button-next::after{
display: none;
 

}
.swiper-button-prev::after{
  display: none;
   
  
  }
  .swiper-button-prev , .swiper-button-next{
    color:gray;
    
    background-color: white;
    transition: all .4s;
    width:40px;
    height:40px;
    border-radius: 100%;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  }
  .swiper-button-prev:hover , .swiper-button-next:hover{
  background-color: rgb(206, 206, 206);      
  }
